<template>
  <el-container>
    <el-main>
      <div class="demo-input-suffix" style="margin-bottom:30px;">
        <template>
          :
          <el-input placeholder="" v-model="realname" clearable style="width:160px;"></el-input>
        </template>
        <template>
          :
          <el-select
            v-model="deptname"
            placeholder=""
            @change="selectgetUsersCentergetAllUserfunction()"
            clearable
          >
            <el-option
              v-for="item in deptlist"
              :key="item.id"
              :label="item.deptName"
              :value="item.id"
            ></el-option>
          </el-select>
        </template>
        <template>
          :
          <el-select
            v-model="bzType"
            placeholder=""
            @change="selectgetUsersCentergetAllUserfunction()"
          >
            <el-option
              v-for="item in bzTypelist"
              :key="item.id"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </template>
        <template>
          :
          <el-date-picker
            v-model="selecttime"
            type="datetimerange"
            :picker-options="pickerOptions"
            range-separator=""
            start-placeholder=""
            end-placeholder=""
            value-format="yyyy-MM-dd"
            align="center"
          ></el-date-picker>
        </template>
        <el-button
          type="primary"
          icon="el-icon-search"
          @click="selectgetUsersCentergetAllUserfunction()"
        ></el-button>

        <el-button
          type="primary"
          icon="el-icon-refresh"
          circle
          style="float:right;"
          @click="updataall"
        ></el-button>
      </div>
      <el-table
        :data="tableData"
        border
        style="width: 100%"
        v-loading="loading"
        :default-sort="{prop: 'date', order: 'descending'}"
      >
        <el-table-column label="" type="index" width="50" align="center"></el-table-column>
        <el-table-column label="" prop="phone" align="center"></el-table-column>
        <el-table-column label="" prop="deptName" align="center"></el-table-column>
        <el-table-column label="" prop="userStatus" align="center">
          <template slot-scope="scope">
            <span v-if="scope.row.userStatus==1"></span>
            <span v-if="scope.row.userStatus==3"></span>
          </template>
        </el-table-column>
        <el-table-column label="" prop="createTime" align="center" sortable></el-table-column>
        <el-table-column label="" align="center" width="200px;">
          <template slot-scope="scope">
            <el-button size="small" type="primary" @click="setUser(scope.row)"></el-button>
            <el-button
              size="small"
              type="danger"
              @click="unbundLing(scope.row.id,scope.row.userStatus)"
              v-if="scope.row.userStatus===1"
            ></el-button>
            <el-button
              size="small"
              type="warning"
              @click="unbundLing(scope.row.id,scope.row.userStatus)"
              v-if="scope.row.userStatus===3"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <div>
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="currentPage"
          :page-sizes="pageSizes"
          :page-size="PageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="totalCount"
        ></el-pagination>
      </div>
    </el-main>
    <el-dialog
      title=""
      :visible.sync="editDialog"
      @close="resetForm('editForm')"
      align="center"
      :close-on-click-modal="false"
    >
      <el-form :model="editForm" :rules="editRules" ref="editForm" label-width="120px">
        <el-form-item label="" prop="id">
          <el-input type="text" v-model.number="editForm.id" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="" prop="editdepartment">
          <el-select
            v-model="editForm.editdepartment"
            placeholder=""
            clearable
            style="width:100%;"
          >
            <el-option
              v-for="item in deptlist"
              :key="item.id"
              :label="item.deptName"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-button type="primary" @click.stop="updateUser('editForm')"></el-button>
      </el-form>
    </el-dialog>
  </el-container>
</template>

<script>
import DATA from "./bloggerpagejson.js"; //
import {
  getUsersCentergetAllUser,
  getDeptallTotal,
  postUsersCenterUpdateBozhuDept,
  postUsersCenterUpdate
} from "@/api/api";
export default {
  name: "BlogGerPage",
  inject: ["reload"],

  //getUsersCentergetAllUserfunction,
  mounted: function() {
    //
    this.tableData = DATA.data.list;
    this.loading = false;
    this.deptlist = DATA.dept;

    // this.getUsersCentergetAllUserfunction();
    // this.getDeptallTotalfunction();
  },
  watch: {},
  data() {
    return {
      deptname: "",
      deptlist: [],
      bzType: 1,
      bzTypelist: [{ id: 1, name: "" }, { id: 3, name: "" }],
      realname: "",
      loading: true,
      tableData: [],
      // 
      currentPage: 1,
      // ()
      totalCount: 0,
      // 
      pageSizes: [15, 20, 30, 40, 50],
      // 
      PageSize: 15,
      selecttime: "",
      pickerOptions: {
        shortcuts: [
          {
            text: "",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit("pick", [start, end]);
            }
          }
        ]
      },
      editDialog: false,
      //
      editForm: {
        id: "",
        editdepartment: ""
      },
      editRules: {
        editdepartment: [
          { required: true, message: "", trigger: "change" }
        ]
      }
    };
  },
  methods: {
    //
    getUsersCentergetAllUserfunction: function(insuserStatus) {
      this.tableData = [];
      if (
        (this.selecttime == "" || this.selecttime == null) &&
        (this.realname == "" || this.realname == null)
      ) {
        getUsersCentergetAllUser({
          deptId: this.deptname,
          userStatus: this.bzType,
          pageNum: this.currentPage,
          pageSize: this.PageSize,
          userRole: 2
        })
          .then(res => {
            if (res.code == 200) {
              if (res.data == undefined) {
                this.$message.info("!");
              } else {
                this.tableData = res.data.list;
                this.totalCount = res.data.total;
                this.$message.success(res.data.msg);
              }
            } else {
              this.$message.info("");
            }
          })
          .catch(err => {});
      } else if (
        (this.selecttime == "" || this.selecttime == null) &&
        this.realname != ""
      ) {
        getUsersCentergetAllUser({
          deptId: this.deptname,
          userStatus: this.bzType,
          phone: this.realname,
          pageNum: this.currentPage,
          pageSize: this.PageSize,
          userRole: 2
        })
          .then(res => {
            if (res.code == 200) {
              if (res.data == undefined) {
                this.$message.info("!");
              } else {
                this.tableData = res.data.list;
                this.totalCount = res.data.total;
                this.$message.success(res.data.msg);
              }
            } else {
              this.$message.info("");
            }
          })
          .catch(err => {});
      } else if (this.selecttime != "" && this.realname == "") {
        getUsersCentergetAllUser({
          deptId: this.deptname,
          userStatus: this.bzType,
          pageNum: this.currentPage,
          pageSize: this.PageSize,
          userRole: 2,
          startTime: this.selecttime[0],
          endTime: this.selecttime[1]
        })
          .then(res => {
            if (res.code == 200) {
              if (res.data == undefined) {
                this.$message.info("!");
              } else {
                this.tableData = res.data.list;
                this.totalCount = res.data.total;
                this.$message.success(res.data.msg);
              }
            } else {
              this.$message.info("");
            }
          })
          .catch(err => {});
      } else {
        getUsersCentergetAllUser({
          deptId: this.deptname,
          userStatus: this.bzType,
          phone: this.realname,
          pageNum: this.currentPage,
          pageSize: this.PageSize,
          userRole: 2,
          startTime: this.selecttime[0],
          endTime: this.selecttime[1]
        })
          .then(res => {
            if (res.code == 200) {
              if (res.data == undefined) {
                this.$message.info("!");
              } else {
                this.tableData = res.data.list;
                this.totalCount = res.data.total;
                this.$message.success(res.data.msg);
              }
            } else {
              this.$message.info("");
            }
          })
          .catch(err => {});
      }
      this.loading = false;
    },
    getDeptallTotalfunction() {
      getDeptallTotal()
        .then(res => {
          if (res.code == 200) {
            if (res.data == undefined) {
              this.$message.info("!");
            } else {
              this.deptlist = res.data;
              this.$message.success(res.msg);
            }
          } else {
            this.$message.info("");
          }
        })
        .catch(err => {});
    },
    //
    setUser: function(row) {
      //
      this.editDialog = true;
      this.editForm.id = row.id;
      this.editForm.editdepartment = row.deptId;
    },
    updateUser: function(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          postUsersCenterUpdateBozhuDept({
            userId: this.editForm.id,
            deptId: this.editForm.editdepartment
          })
            .then(res => {
              if (res.code == 200) {
                this.resetForm(formName);
                this.$message.success(res.msg);
                this.getUsersCentergetAllUserfunction();
              } else {
                this.$message.info(res.msg);
              }
            })
            .catch(err => {});
        } else {
          return false;
        }
      });
    },

    unbundLing(ins, insuserStatus) {
      this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
        center: true
      })
        .then(() => {
          let that = this;
          postUsersCenterUpdate({
            userId: ins,
            userStatus: insuserStatus,
            userRole: 2
          })
            .then(res => {
              if (res.code == 200) {
                that
                  .$confirm(res.msg, "", {
                    confirmButtonText: "",
                    showClose: false,
                    showCancelButton: false,
                    type: "warning",
                    center: true
                  })
                  .then(() => {
                    that.getUsersCentergetAllUserfunction();
                  });
              } else {
                that.$message.info(res.msg);
              }
            })
            .catch(err => {});
        })
        .catch(() => {
          this.$message.info("");
        });
    },

    selectgetUsersCentergetAllUserfunction: function() {
      this.currentPage = 1;
      this.getUsersCentergetAllUserfunction();
    },

    //
    // 
    handleSizeChange(val) {
      // 
      this.PageSize = val;
      // 
      this.currentPage = 1;
      this.getUsersCentergetAllUserfunction();
    },
    // 
    handleCurrentChange(val) {
      // 
      this.currentPage = val;
      this.getUsersCentergetAllUserfunction();
    },
    resetForm: function(formName) {
      if (formName == "editForm") {
        this.$refs["editForm"].resetFields();
        //
        this.editDialog = false;
      }
    },
    updataall() {
      this.reload(); //
    }
  }
};
</script>
<style scoped>
</style>


