<template>
  <el-container>
    <el-main>
      <div class="demo-input-suffix" style="margin-bottom:30px;">
        <template>
          :
          <el-select v-model="rwType" placeholder="" @change="selectOrderCenterAllfunction()">
            <el-option
              v-for="item in rwTypelist"
              :key="item.id"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </template>
        <template>
          :
          <el-input placeholder="" v-model="rwtitle" clearable style="width:160px;"></el-input>
        </template>
        <template>
          :
          <el-input placeholder="" v-model="publisher" clearable style="width:160px;"></el-input>
        </template>
        <template>
          :
          <el-select
            v-model="rwStatus"
            placeholder=""
            clearable
            @change="selectOrderCenterAllfunction()"
          >
            <el-option
              v-for="item in rwStatuslist"
              :key="item.id"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </template>
        <template>
          :
          <el-date-picker
            v-model="selecttime"
            type="datetimerange"
            :picker-options="pickerOptions"
            range-separator=""
            start-placeholder=""
            end-placeholder=""
            value-format="yyyy-MM-dd"
            align="center"
          ></el-date-picker>
        </template>
        <el-button type="primary" icon="el-icon-search" @click="selectOrderCenterAllfunction()"></el-button>
        <el-button
          type="primary"
          icon="el-icon-refresh"
          circle
          style="float:right;"
          @click="updataall"
        ></el-button>
      </div>
      <el-table
        :data="tableData"
        border
        style="width: 100%"
        v-loading="loading"
        :default-sort="{prop: 'date', order: 'descending'}"
      >
        <el-table-column label="" type="index" width="50" align="center"></el-table-column>
        <el-table-column label="" prop="orderNo,platformUrl" align="center">
          <template slot-scope="scope">
            <a
              :href="scope.row.platformUrl"
              class="buttonText"
              target="_blank"
            >{{scope.row.orderNo}}</a>
          </template>
        </el-table-column>
        <el-table-column label="" prop="platform" align="center">
          <template slot-scope="scope">
            <span v-if="scope.row.platform==1"></span>
          </template>
        </el-table-column>
        <el-table-column label="" prop="title" align="center">
          <template slot-scope="scope">
            <el-popover trigger="hover" placement="top">
              <span>{{ scope.row.title }}</span>
              <div slot="reference" class="name-wrapper">
                <a @click="turnrouter(scope.row.taskId)" class="buttonText">{{scope.row.title}}</a>
              </div>
            </el-popover>
          </template>
        </el-table-column>
        <el-table-column label="" prop="nickName" align="center"></el-table-column>
        <el-table-column label="" prop="realName" align="center"></el-table-column>
        <el-table-column label="" prop="orderNum" align="center"></el-table-column>
        <el-table-column label="" prop="orderStatus" align="center">
          <template slot-scope="scope">
            <span
              v-if="scope.row.orderStatus==1"
              @click="openshowDialog(scope.row)"
              class="buttonText"
            ></span>
            <span
              v-if="scope.row.orderStatus==2"
              @click="openshowDialog(scope.row)"
              class="buttonText"
            ></span>
            <span
              v-if="scope.row.orderStatus==3"
              @click="openshowDialog(scope.row)"
              class="buttonText"
            ></span>
            <span
              v-if="scope.row.orderStatus==4"
              @click="openshowDialog(scope.row)"
              class="buttonText"
            ></span>
            <span
              v-if="scope.row.orderStatus==5"
              @click="openshowDialog(scope.row)"
              class="buttonText"
            ></span>
            <span
              v-if="scope.row.orderStatus==12"
              @click="openshowDialog(scope.row)"
              class="buttonText"
            ></span>
          </template>
        </el-table-column>
        <el-table-column label="" prop="orderPayStatus" align="center">
          <template slot-scope="scope">
            <span v-if="scope.row.orderPayStatus==0"></span>
            <span v-if="scope.row.orderPayStatus==1"></span>
            <span v-if="scope.row.orderPayStatus==2"></span>
            <span v-if="scope.row.orderPayStatus==3"></span>
            <span v-if="scope.row.orderPayStatus==4"></span>
          </template>
        </el-table-column>
        <el-table-column label="" prop="deptName" align="center"></el-table-column>
        <el-table-column label="" prop="startTime" align="center" sortable></el-table-column>
        <el-table-column label="" prop="submitTime" align="center" sortable></el-table-column>
        <el-table-column
          label=""
          prop="checkTime"
          align="center"
          v-if="rwStatus==3||rwStatus==4"
          sortable
        ></el-table-column>
        <el-table-column label="" prop="appealTime" align="center" v-if="rwStatus==5" sortable></el-table-column>
        <el-table-column label="" align="center" width="180px;">
          <template slot-scope="scope">
            <el-button
              size="small"
              type="danger"
              @click="abnormal(scope.row)"
              v-if="scope.row.orderStatus==2&&(scope.row.orderPayStatus==0||scope.row.orderPayStatus==1)"
            ></el-button>
            <el-button
              size="small"
              type="success"
              @click="abnormal(scope.row)"
              v-if="scope.row.orderStatus==3||scope.row.orderStatus==4"
            ></el-button>
            <el-button
              size="small"
              type="primary"
              @click="auDit(scope.row)"
              v-if="scope.row.orderStatus==5"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>

      <!--  -->
      <div class="tabListPage">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="currentPage"
          :page-sizes="pageSizes"
          :page-size="PageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="totalCount"
        ></el-pagination>
      </div>
    </el-main>
    <el-dialog title="" :visible.sync="showDialog" align="center" :close-on-click-modal="false">
      <div class="pu">
        <p class="cltitle"></p>
        <p>:{{jdpeople}}</p>
        <p>:{{jdnum}}</p>
        <p>:{{jdtime}}</p>
      </div>
      <div v-if="this.showorderStatus!=1">
        <div class="pu">
          <p class="cltitle"></p>
          <p>:{{fntaskformStr}}</p>
          <div class="flexdiv">
            <div class="flexdivch" v-if="fncomments!=''">
              <p></p>
              <ul v-for="(items,index) in fncomments" :key="index">
                <li v-for="(item,indexs) in items" :key="indexs">
                  <p>:{{item.screenName}}</p>
                  <p class="fncom">:{{item.text}}</p>
                  <p>:{{item.publishTime}}</p>
                </li>
              </ul>
            </div>
            <div class="flexdivch" v-if="fnlikes!=''">
              <p></p>
              <ul v-for="(items,index) in fnlikes" :key="index">
                <li v-for="(item,indexs) in items" :key="indexs">
                  <p>:{{item.screenName}}</p>
                  <p>:{{item.createdAt}}</p>
                </li>
              </ul>
            </div>
            <div class="flexdivch" v-if="fnforwards!=''">
              <p></p>
              <ul v-for="(items,index) in fnforwards" :key="index">
                <li v-for="(item,indexs) in items" :key="indexs">
                  <p>:{{item.screenName}}</p>
                  <p>:{{item.createdAt}}</p>
                </li>
              </ul>
            </div>
          </div>
          <p v-if="this.showorderStatus!=3">:{{fntime}}</p>
        </div>
        <div class="pu" v-if="xtappealExplainlist!=null||xtappealExplainlist!=''">
          <p class="cltitle"></p>
          <el-table :data="xtappealExplainlist" border style="width: 100%">
            <el-table-column label="" type="index" width="50" align="center"></el-table-column>
            <el-table-column label="" prop="checkReason" align="center"></el-table-column>
            <el-table-column label="" prop="checkStatusStr" align="center"></el-table-column>
            <el-table-column label="" prop="createTime" align="center"></el-table-column>
          </el-table>
        </div>
        <div v-if="sulist!=''">
          <p class="cltitle"></p>
          <div v-for="(items,index) in sulist" :key="index">
            <div v-if="items.role==1" class="listmsg">
              <i class="el-icon-user usericon">
                <span class="user">{{jdpeople}}</span>
              </i>
              <p></p>
              <div id="showimage">
                <div class="showimage">
                  <ul class="imglist" id="imagelist">
                    <li v-for="(item,index) in items.pics" :key="index">
                      <img
                        :src="item.img"
                        referrer="no-referrer|origin|unsafe-url"
                        @click.stop="showbigimg(item.img)"
                      >
                    </li>
                  </ul>
                </div>
              </div>
              <p></p>
              <div id="showcontent">
                <div class="showcontent">{{items.content}}</div>
              </div>
              <p>:{{items.createTime}}</p>
            </div>
            <div v-if="items.role==2" class="listmsgs">
              <i class="el-icon-user-solid adminicon">
                <span class="admin">{{user_name}}</span>
              </i>
              <p></p>
              <div id="showcontent">
                <div class="showcontent">{{items.content}}</div>
              </div>
              <p>:{{items.createTime}}</p>
            </div>
          </div>
        </div>
      </div>
    </el-dialog>
    <el-dialog
      title=""
      :visible.sync="editDialog"
      @close="resetForm('editForm')"
      align="center"
      :before-close="handleClose"
    >
      <div v-for="(items,index) in sulist" :key="index">
        <div v-if="items.role==1" class="listmsg">
          <i class="el-icon-user usericon">
            <span class="user">{{jdpeople}}</span>
          </i>
          <p></p>
          <div id="showimage">
            <div class="showimage">
              <ul class="imglist" id="imagelist">
                <li v-for="(item,index) in items.pics" :key="index">
                  <img
                    :src="item.img"
                    referrer="no-referrer|origin|unsafe-url"
                    @click.stop="showbigimg(item.img)"
                  >
                </li>
              </ul>
            </div>
          </div>
          <p></p>
          <div id="showcontent">
            <div class="showcontent">{{items.content}}</div>
          </div>
          <p>:{{items.createTime}}</p>
        </div>
        <div v-if="items.role==2" class="listmsgs">
          <i class="el-icon-user-solid adminicon">
            <span class="admin">{{user_name}}</span>
          </i>
          <p></p>
          <div id="showcontent">
            <div class="showcontent">{{items.content}}</div>
          </div>
          <p>:{{items.createTime}}</p>
        </div>
      </div>
      <el-button type="success" :disabled="true" v-if="orderStatus==2"></el-button>
      <el-button type="primary" @click.stop="pass()" v-if="orderStatus!=2"></el-button>
      <el-button type="warning" @click.stop="nopass()" v-if="orderStatus!=2"></el-button>
    </el-dialog>
    <el-dialog :visible.sync="showimgDialog" align="center" :close-on-click-modal="false">
      <div style="width:50%;height:50%;">
        <img :src="showimg" :alt="showimg" style="width:100%;height:100%;">
      </div>
    </el-dialog>
  </el-container>
</template>

<script>
import DATA from "./orderpagejson.js"; //
import {
  getOrderCenterAll,
  postOrderCenterCompleteToFailure,
  postOrderCenteranyToComplete,
  getOrderCenterOrderItem,
  postOrderCenterAuditFailed
} from "@/api/api";
export default {
  name: "OrderPage",
  inject: ["reload"],

  //getOrderCenterAllfunction,
  mounted: function() {
    //
    this.tableData = DATA.data.list;
    this.loading = false;

    // this.getOrderCenterAllfunction();
  },
  watch: {},
  data() {
    return {
      user_name: window.sessionStorage.getItem("handleUserName"),
      showDialog: false,
      showorderStatus: "",
      //
      jdpeople: "",
      jdtime: "",
      jdnum: "",
      //
      fntaskformStr: "",
      fntime: "",
      fncomments: [],
      fnforwards: [],
      fnlikes: [],
      //
      xtappealExplainlist: [],

      orderStatus: "",
      id: "",
      sulist: [],
      editDialog: false,
      showimgDialog: false,
      showimg: "",

      //
      loading: true,
      tableData: [],
      // 
      currentPage: 1,
      // ()
      totalCount: 0,
      // 
      pageSizes: [15, 20, 30, 40, 50],
      // 
      PageSize: 15,
      //
      routeorderNo: this.$route.query.orderNo,
      rwtitle: "",
      publisher: "",
      rwType: 1,
      rwTypelist: [{ id: 1, name: "" }],
      rwStatus: "",
      rwStatuslist: [
        { id: 1, name: "" },
        { id: 2, name: "" },
        { id: 3, name: "" },
        { id: 4, name: "" },
        { id: 5, name: "" }
      ],

      selecttime: "",
      pickerOptions: {
        shortcuts: [
          {
            text: "",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit("pick", [start, end]);
            }
          }
        ]
      }
    };
  },
  methods: {
    showbigimg(ins) {
      this.showimgDialog = true;
      this.showimg = ins;
    },
    //
    getOrderCenterAllfunction: function() {
      this.tableData = [];
      if (this.selecttime == "" || this.selecttime == null) {
        getOrderCenterAll({
          pageNum: this.currentPage,
          pageSize: this.PageSize,
          startTime: "",
          endTime: "",
          orderStatus: this.rwStatus,
          realName: this.publisher,
          title: this.rwtitle,
          platform: this.rwType,
          orderNo: this.routeorderNo
        })
          .then(res => {
            if (res.code == 200) {
              if (res.data == undefined) {
                this.$message.info("!");
              } else {
                this.tableData = res.data.list;
                this.totalCount = res.data.total;
                this.$message.success(res.msg);
              }
            } else {
              this.$message.error(res.msg);
            }
            this.loading = false;
          })
          .catch(err => {});
      } else {
        getOrderCenterAll({
          pageNum: this.currentPage,
          pageSize: this.PageSize,
          startTime: this.selecttime[0],
          endTime: this.selecttime[1],
          orderStatus: this.rwStatus,
          realName: this.publisher,
          title: this.rwtitle,
          platform: this.rwType,
          orderNo: this.routeorderNo
        })
          .then(res => {
            if (res.code == 200) {
              if (res.data == undefined) {
                this.$message.info("!");
              } else {
                this.tableData = res.data.list;
                this.totalCount = res.data.total;
                this.$message.success(res.msg);
              }
            } else {
              this.$message.error(res.msg);
            }
            this.loading = false;
          })
          .catch(err => {});
      }
    },
    selectOrderCenterAllfunction: function(ins) {
      this.currentPage = 1;
      this.getOrderCenterAllfunction();
    },

    //
    // 
    handleSizeChange(val) {
      // 
      this.PageSize = val;
      // 
      this.currentPage = 1;
      this.getOrderCenterAllfunction();
    },
    // 
    handleCurrentChange(val) {
      // 
      this.currentPage = val;
      this.getOrderCenterAllfunction();
    },
    updataall() {
      this.reload(); //
      this.$route.query.orderNo = "";
    },
    //
    abnormal: function(row) {
      if (row.orderStatus == 2) {
        this.$prompt("", "", {
          confirmButtonText: "",
          cancelButtonText: "",
          inputPattern: /^[\s\S]*.*[^\s][\s\S]*$/,
          inputErrorMessage: "",
          center: true
        })
          .then(({ value }) => {
            postOrderCenterCompleteToFailure({
              orderId: row.id,
              content: value
            })
              .then(res => {
                if (res.code == 200) {
                  this.$message.success(res.msg);
                  this.getOrderCenterAllfunction();
                } else {
                  this.$message.info(res.msg);
                }
              })
              .catch(err => {});
          })
          .catch(() => {
            this.$message.info("");
          });
      } else {
        this.$confirm("?", "", {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning",
          center: true
        })
          .then(() => {
            postOrderCenteranyToComplete({
              orderId: row.id
            })
              .then(res => {
                if (res.code == 200) {
                  this.$message.success(res.msg);
                  this.getOrderCenterAllfunction();
                } else {
                  this.$message.info(res.msg);
                }
              })
              .catch(err => {});
          })
          .catch(() => {
            this.$message.info("");
          });
      }
    },
    handleClose(done) {
      this.$confirm("")
        .then(_ => {
          done();
        })
        .catch(_ => {});
    },
    openshowDialog: function(row) {
      //
      this.showDialog = true;
      this.showorderStatus = row.orderStatus;
      getOrderCenterOrderItem({
        orderId: row.id
      })
        .then(res => {
          if (res.code == 200) {
            if (res.data != "") {
              //
              this.jdpeople = res.data.user.realName;
              this.jdtime = res.data.userOrder.startTime;
              this.jdnum = res.data.userOrder.orderNum;
              //
              this.fntaskformStr = res.data.task.taskformStr;
              this.fntime = res.data.userOrder.submitTime;
              this.fncomments = res.data.doTaskItem.comments;
              this.fnforwards = res.data.doTaskItem.forwards;
              this.fnlikes = res.data.doTaskItem.likes;
              //
              this.xtappealExplainlist = res.data.userOrderCheckList;

              this.sulist = res.data.appealDetails;
              for (var i = 0; i < this.sulist.length; i++) {
                if (this.sulist[i].pics != null) {
                  var b = this.sulist[i].pics.replace(/\[/g, ""); //
                  var c = b.replace(/\]/g, ""); //
                  var d = c.replace(/\ /g, ""); //
                  var e = d.split(","); //,
                  var all = [];
                  for (var j = 0; j < e.length; j++) {
                    all.push({ img: e[j] }); //list
                  }
                  this.sulist[i].pics = all;
                }
              }
            }
          } else {
            this.$message.error(res.msg);
          }
        })
        .catch(err => {});
    },
    //
    auDit: function(row) {
      //
      this.editDialog = true;
      this.sulist = [];
      this.jdpeople = row.realName;
      this.orderStatus = row.orderStatus;
      this.id = row.id;
      getOrderCenterOrderItem({
        orderId: row.id
      })
        .then(res => {
          if (res.code == 200) {
            if (res.data != "") {
              this.sulist = res.data.appealDetails;
              for (var i = 0; i < this.sulist.length; i++) {
                if (this.sulist[i].pics != null) {
                  var b = this.sulist[i].pics.replace(/\[/g, ""); //
                  var c = b.replace(/\]/g, ""); //
                  var d = c.replace(/\ /g, ""); //
                  var e = d.split(","); //,
                  var all = [];
                  for (var j = 0; j < e.length; j++) {
                    all.push({ img: e[j] }); //list
                  }
                  this.sulist[i].pics = all;
                }
              }
            }
          } else {
            this.$message.error(res.msg);
          }
        })
        .catch(err => {});
    },

    pass: function() {
      postOrderCenteranyToComplete({
        orderId: this.id
      })
        .then(res => {
          if (res.code == 200) {
            this.$message.success(res.msg);
            this.getOrderCenterAllfunction();
            this.resetForm("editForm");
          } else {
            this.$message.info(res.msg);
          }
        })
        .catch(err => {});
    },

    nopass: function() {
      this.$prompt("", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        inputPattern: /^[\s\S]*.*[^\s][\s\S]*$/,
        inputErrorMessage: "",
        center: true
      })
        .then(({ value }) => {
          postOrderCenterAuditFailed({
            orderId: this.id,
            content: value
          })
            .then(res => {
              if (res.code == 200) {
                this.$message.success(res.msg);
                this.getOrderCenterAllfunction();
                this.resetForm("editForm");
              } else {
                this.$message.info(res.msg);
              }
            })
            .catch(err => {});
        })
        .catch(() => {
          this.$message.info("");
        });
    },
    resetForm: function(formName) {
      if (formName == "editForm") {
        //
        this.editDialog = false;
      }
    },

    //
    turnrouter(ins) {
      this.$router.push({
        path: "/taskpage",
        query: {
          taskId: ins
        }
      });
    }
  }
};
</script>
<style scoped>
