  position: absolute;
  left: 4%;
  font-size: 36px;
}
.user {
  position: absolute;
  width: 70px;
  font-size: 16px;
  top: 100%;
  text-overflow: -o-ellipsis-lastline;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
}
.adminicon {
  position: absolute;
  right: 4%;
  font-size: 36px;
}
.admin {
  position: absolute;
  width: 70px;
  right: -20%;
  font-size: 16px;
  top: 100%;
  text-overflow: -o-ellipsis-lastline;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
}
.pu {
  background: #ebeef5;
  padding: 5px;
  margin: 5px 0px;
  width: 80%;
}
.cltitle {
  padding: 0px 15px;
  font-size: 18px;
  color: black;
}
.flexdiv {
  display: flex;
  flex-direction: row;
}
.flexdivch {
  width: 32%;
  color: #409eff;
  background: #fff;
  flex: 1;
  margin: 0px 2px;
}
.flexdivch ul {
  padding: 2px;
}
.flexdivch ul li {
  text-align: left;
  padding: 0px 5px;
}
.fncom {
  text-overflow: -o-ellipsis-lastline;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}
ul {
  padding: 0px;
}
ul li {
  list-style-type: none;
}
ul.imglist {
  margin: 0 auto;
  display: inline-block;
}
ul.imglist li {
  float: left;
  padding: 4px 8px;
  width: 200px;
  height: 200px;
  list-style-type: none;
}
ul.imglist li img {
  object-fit: cover;
  display: block;
  width: 100%;
  height: 100%;
}

#imagelist li:hover img {
  transform: scale(2);
}
</style>


