<template>
  <el-container>
    <el-main>
      <div class="demo-input-suffix" style="margin-bottom:30px;">
        <template>
          :
          <el-input placeholder="" v-model="totalnumber" clearable style="width:20%;"></el-input>
        </template>

        <el-button type="primary" icon="el-icon-zoom-in" @click="makejhm()"></el-button>
        <el-button
          type="primary"
          icon="el-icon-refresh"
          circle
          style="float:right;"
          @click="updataall"
        ></el-button>
      </div>
      <el-table
        :data="tableData.slice((currentPage-1)*PageSize,currentPage*PageSize)"
        border
        style="width: 100%"
        v-loading="loading"
      >
        <el-table-column label="" type="index" width="50" align="center"></el-table-column>
        <el-table-column label="" prop="codeNum" align="center"></el-table-column>
      </el-table>

      <!--  -->
      <div>
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="currentPage"
          :page-sizes="pageSizes"
          :page-size="PageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="totalCount"
        ></el-pagination>
      </div>
    </el-main>
  </el-container>
</template>


<script>
import DATA from "./activationcodepagejson.js"; //
import { getCodeSend } from "@/api/api";
export default {
  name: "ActivationCodePage",
  inject: ["reload"],

  //getCodeSendfunction,
  mounted: function() {
    //
    this.tableData = DATA.data;
    this.loading = false;
    // this.getCodeSendfunction();
  },
  watch: {},
  data() {
    return {
      loading: true,
      totalnumber: 15,
      tableData: [],
      // 
      currentPage: 1,
      // ()
      totalCount: 0,
      // 
      pageSizes: [15, 20, 30, 40, 50],
      // 
      PageSize: 15
    };
  },
  methods: {
    //
    getCodeSendfunction: function() {
      this.tableData = [];
      getCodeSend({
        total: this.totalnumber
      })
        .then(res => {
          if (res.code == 200) {
            if (res.data == undefined) {
              this.$message.info("!");
            } else {
              this.tableData = res.data;
              this.totalCount = res.data.length;
              this.$message.success(res.msg);
            }
          } else {
            this.$message.error(res.msg);
          }
          this.loading = false;
        })
        .catch(err => {});
    },
    makejhm: function() {
      if (
        (this.totalnumber <= 0) | (this.totalnumber == "") ||
        this.totalnumber == null
      ) {
        this.$message.info("");
      } else {
        this.currentPage = 1;
        this.getCodeSendfunction();
      }
    },
    handleSizeChange(val) {
      // 
      this.PageSize = val;
      // 
      this.currentPage = 1;
    },
    // 
    handleCurrentChange(val) {
      // 
      this.currentPage = val;
    },
    updataall() {
      this.reload(); //
    }
  }
};
</script>
<style scoped>
</style>


