<template>
  <el-container>
    <el-main>
      <div class="demo-input-suffix" style="margin-bottom:30px;">
        <el-button
          type="primary"
          icon="el-icon-back"
          style="float:right;margin-bottom:30px;"
          @click="callback"
        ></el-button>
      </div>
      <el-table
        :data="tableData"
        border
        style="width: 100%"
        v-loading="loading"
        :default-sort="{prop: 'date', order: 'descending'}"
      >
        <el-table-column label="" type="index" width="50" align="center"></el-table-column>
        <el-table-column label="" prop="platformType" align="center">
          <template slot-scope="scope">
            <span v-if="scope.row.platformType==1"></span>
          </template>
        </el-table-column>
        <el-table-column label="(UID)" prop="accountUid" align="center"></el-table-column>
        <el-table-column label="" prop="accountNickName" align="center"></el-table-column>
        <el-table-column label="" prop="createTime" align="center" sortable></el-table-column>
      </el-table>
    </el-main>
  </el-container>
</template>

<script>
import DATA from "./userdetailspagejson.js"; //
import { getUsersCentergetUserPlatform } from "@/api/api";
export default {
  name: "UserDetailsPage",
  props: ["sendid", "sendids"],
  //getUsersCentergetUserPlatformfunction,
  mounted: function() {
    //
    this.tableData = DATA.data;
    this.loading = false;
    // this.getUsersCentergetUserPlatformfunction();
  },

  data() {
    return {
      loading: true,
      tableData: []
    };
  },
  methods: {
    //
    getUsersCentergetUserPlatformfunction: function() {
      getUsersCentergetUserPlatform({
        userId: this.sendid,
        userStatus: this.sendids
      })
        .then(res => {
          if (res.code == 200) {
            if (res.data == undefined) {
              this.$message.info("!");
            } else {
              this.tableData = res.data;
              this.$message.success(res.msg);
            }
          } else {
            this.$message.info("");
          }
          this.loading = false;
        })
        .catch(err => {});
    },
    callback() {
      this.$emit("listenToChildEvent", "false");
    }
  }
};
</script>
<style scoped>
.name-wrapper {
  cursor: pointer;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.buttonText {
  text-decoration: none;
  cursor: pointer;
  color: #409eff !important;
}
</style>


