import Vue from 'vue'
import Router from 'vue-router'
import store from '../store/index'
import NProgress from 'nprogress'
import 'nprogress/nprogress.css'
import LoGin from '@/pages/login/LoGin'
import TheLayout from '@/pages/layout/TheLayout'

import TaskPage from '@/pages/task/TaskPage'
import OrderPage from '@/pages/order/OrderPage'
import BlogGerpage from '@/pages/blogger/BlogGerpage'
import SettlementPage from '@/pages/settlement/SettlementPage'
import UserPage from '@/pages/user/UserPage'
import ActivationCodePage from '@/pages/setting/ActivationCodePage'
import DepartmentPage from '@/pages/department/DepartmentPage'


import AppError401 from '@/pages/error/AppError401'
import AppError404 from '@/pages/error/AppError404'

Vue.use(Router)
/*  */
const routes = [{
    path: '/',
    name: '',
    component: LoGin,
    meta: {
      auth: false
    }
  },
  {
    path: '/',
    component: TheLayout,
    icon: 'el-icon-s-home',
    menu: true,
    children: [{
      path: '/taskpage',
      name: '',
      component: TaskPage,
      meta: {
        requiresAuth: true,
        title: ''
      },
    }]
  },
  {
    path: '/',
    component: TheLayout,
    icon: 'el-icon-star-on',
    menu: true,
    children: [{
      path: '/orderpage',
      name: '',
      component: OrderPage,
      meta: {
        requiresAuth: true,
        title: ''
      },
    }]
  },
  {
    path: '/',
    component: TheLayout,
    icon: 'el-icon-star-on',
    menu: true,
    children: [{
      path: '/settlementpage',
      name: '',
      component: SettlementPage,
      meta: {
        requiresAuth: true,
        title: ''
      },
    }]
  },
  {
    path: '/',
    component: TheLayout,
    name: '',
    icon: 'el-icon-star-on',
    menu: true,
    children: [{
      path: '/userpage',
      name: '',
      component: UserPage,
      meta: {
        requiresAuth: true,
        title: ''
      }
    }, {
      path: '/bloggerpage',
      name: '',
      component: BlogGerpage,
      meta: {
        requiresAuth: true,
        title: ''
      }
    }, ]
  },
  {
    path: '/',
    component: TheLayout,
    name: '',
    icon: 'el-icon-star-on',
    menu: true,
    children: [{
        path: '/departmentpage',
        name: '',
        component: DepartmentPage,
        meta: {
          requiresAuth: true,
          title: ''
        }
      },
      {
        path: '/activationcodepage',
        name: '',
        component: ActivationCodePage,
        meta: {
          requiresAuth: true,
          title: ''
        }
      },
    ]
  },
  {
    path: '/error/401',
    name: '401',
    meta: {
      errorPage: true
    },
    component: AppError401
  }, {
    path: '*',
    name: '404',
    meta: {
      errorPage: true
    },
    component: AppError404
  }
]

//


// token
if (sessionStorage.getItem('token')) {
  store.commit('set_token', sessionStorage.getItem('token'))
}

const router = new Router({
  routes
});

router.beforeEach((to, from, next) => {
  // start progress bar
  NProgress.start()

  if (to.matched.some(r => r.meta.requiresAuth)) { //requireAuth meta:{requiresAuth:true}
    if (store.state.token) {
      next();
    } else {
      next({
        path: '/',
      })
    }
  } else {
    next();
  }
});
router.afterEach(() => {
  // finish progress bar
  NProgress.done()
})


export default router



