<template>
  <el-container>
    <el-main>
      <div class="demo-input-suffix" style="margin-bottom:30px;">
        <el-button type="primary" icon="el-icon-plus" @click="addDialog = true"></el-button>
        <el-button
          type="primary"
          icon="el-icon-refresh"
          circle
          style="float:right;"
          @click="updataall"
        ></el-button>
      </div>
      <el-table :data="tableData" border style="width: 100%" v-loading="loading">
        <el-table-column label="" type="index" width="50" align="center"></el-table-column>
        <el-table-column label="" prop="deptName" align="center"></el-table-column>
        <el-table-column label="" align="center" width="180px;">
          <template slot-scope="scope">
            <el-button size="small" type="primary" @click="setUser(scope.row)"></el-button>
            <el-button size="small" type="danger" @click="deleteUser(scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>

      <!--  -->
      <div>
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="currentPage"
          :page-sizes="pageSizes"
          :page-size="PageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="totalCount"
        ></el-pagination>
      </div>
    </el-main>
    <el-dialog
      title=""
      :visible.sync="addDialog"
      @close="resetForm('addForm')"
      align="center"
      :close-on-click-modal="false"
    >
      <el-form :model="addForm" :rules="addRules" ref="addForm" label-width="120px">
        <el-form-item label="" prop="adddepartment">
          <el-input type="text" v-model="addForm.adddepartment" clearable></el-input>
        </el-form-item>

        <el-button type="primary" @click.stop="submitForm('addForm')"></el-button>
      </el-form>
    </el-dialog>
    <el-dialog
      title=""
      :visible.sync="editDialog"
      @close="resetForm('editForm')"
      align="center"
      :close-on-click-modal="false"
    >
      <el-form :model="editForm" :rules="editRules" ref="editForm" label-width="120px">
        <el-form-item label="" prop="id">
          <el-input type="text" v-model.number="editForm.id" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="" prop="editdepartment">
          <el-input type="text" v-model="editForm.editdepartment" clearable></el-input>
        </el-form-item>
        <el-button type="primary" @click.stop="updateUser('editForm')"></el-button>
      </el-form>
    </el-dialog>
  </el-container>
</template>

<script>
import DATA from "./departmentpagejson.js"; //
import {
  getDeptAll,
  postDeptInsert,
  postDeptUpdate,
  delDeptdelete
} from "@/api/api";
export default {
  name: "DepartmentPage",
  inject: ["reload"],

  //getDeptAllfunction,
  mounted: function() {
    //
    this.tableData = DATA.data.list;
    this.loading = false;

    // this.getDeptAllfunction();
  },
  watch: {},
  data() {
    return {
      loading: true,
      tableData: [],
      // 
      currentPage: 1,
      // ()
      totalCount: 0,
      // 
      pageSizes: [15, 20, 30, 40, 50],
      // 
      PageSize: 15,

      //
      addDialog: false,
      //
      editDialog: false,
      addForm: {
        adddepartment: ""
      },
      addRules: {
        adddepartment: [
          { required: true, message: "", trigger: "blur" }
        ]
      },
      //
      editForm: {
        id: "",
        editdepartment: ""
      },
      editRules: {
        editdepartment: [
          { required: true, message: "", trigger: "blur" }
        ]
      }
    };
  },
  methods: {
    //
    getDeptAllfunction: function(inspayStatus) {
      this.tableData = [];
      getDeptAll({
        pageNum: this.currentPage,
        pageSize: this.PageSize
      })
        .then(res => {
          if (res.code == 200) {
            if (res.data.list.length == 0) {
              this.$message.info("!");
              this.tableData = res.data.list;
              this.totalCount = res.data.total;
            } else {
              this.tableData = res.data.list;
              this.totalCount = res.data.total;
              this.$message.success(res.msg);
            }
          } else {
            this.$message.error(res.msg);
          }
          this.loading = false;
        })
        .catch(err => {});
    },

    handleSizeChange(val) {
      // 
      this.PageSize = val;
      // 
      this.currentPage = 1;
      this.getDeptAllfunction();
    },
    // 
    handleCurrentChange(val) {
      // 
      this.currentPage = val;
      this.getDeptAllfunction();
    },

    //
    submitForm: function(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          postDeptInsert({
            deptName: this.addForm.adddepartment
          })
            .then(res => {
              if (res.code == 200) {
                this.$message.success(res.msg);
                this.getDeptAllfunction();
                this.resetForm(formName);
              } else {
                this.$message.info(res.msg);
              }
            })
            .catch(err => {});
        } else {
          return false;
        }
      });
    },
    //
    setUser: function(row) {
      //
      this.editDialog = true;
      //this.editForm = Object.assign({}, row);
      this.editForm.id = row.id;
      this.editForm.editdepartment = row.deptName;
    },

    updateUser: function(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          postDeptUpdate({
            deptId: this.editForm.id,
            deptName: this.editForm.editdepartment
          })
            .then(res => {
              if (res.code == 200) {
                this.$message.success(res.msg);
                this.getDeptAllfunction();
                this.resetForm(formName);
              } else {
                this.$message.info(res.msg);
              }
            })
            .catch(err => {});
        } else {
          return false;
        }
      });
    },

    //
    deleteUser: function(row) {
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
        center: true
      })
        .then(() => {
          delDeptdelete({
            deptId: row.id
          })
            .then(res => {
              if (res.code == 200) {
                this.$message.success(res.msg);
                this.getDeptAllfunction();
              } else {
                this.$message.info(res.msg);
              }
            })
            .catch(err => {});
        })
        .catch(() => {
          this.$message.info("");
        });
    },
    resetForm: function(formName) {
      if (formName == "addForm") {
        this.$refs["addForm"].resetFields();
        //
        this.addDialog = false;
      } else if (formName == "editForm") {
        this.$refs["editForm"].resetFields();
        //
        this.editDialog = false;
      }
    },
    updataall() {
      this.reload(); //
    }
  }
};
</script>
<style scoped>
</style>


