<template>
  <el-container>
    <el-main>
      <div class="demo-input-suffix" style="margin-bottom:30px;">
        <template>
          :
          <el-select v-model="rwType" placeholder="" @change="selectTaskCenterAllfunction()">
            <el-option
              v-for="item in rwTypelist"
              :key="item.id"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </template>
        <template>
          :
          <el-input placeholder="" v-model="rwtitle" clearable style="width:160px;"></el-input>
        </template>
        <template>
          :
          <el-select
            v-model="rwStatus"
            placeholder=""
            clearable
            @change="selectTaskCenterAllfunction()"
          >
            <el-option
              v-for="item in rwStatuslist"
              :key="item.id"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </template>
        <template>
          :
          <el-input placeholder="" v-model="publisher" clearable style="width:160px;"></el-input>
        </template>
        <template>
          :
          <el-date-picker
            v-model="selecttime"
            type="datetimerange"
            :picker-options="pickerOptions"
            range-separator=""
            start-placeholder=""
            end-placeholder=""
            value-format="yyyy-MM-dd"
            align="center"
          ></el-date-picker>
        </template>
        <el-button type="primary" icon="el-icon-search" @click="selectTaskCenterAllfunction()"></el-button>
        <el-button
          type="primary"
          icon="el-icon-refresh"
          circle
          style="float:right;"
          @click="updataall"
        ></el-button>
      </div>
      <el-table
        :data="tableData"
        border
        style="width: 100%"
        v-loading="loading"
        :default-sort="{prop: 'date', order: 'descending'}"
      >
        <el-table-column label="" type="index" width="50" align="center"></el-table-column>
        <el-table-column label="" prop="taskNo" align="center"></el-table-column>
        <el-table-column label="" prop="platform" align="center">
          <template slot-scope="scope">
            <span v-if="scope.row.platform==1"></span>
          </template>
        </el-table-column>
        <el-table-column label="" prop="title" align="center">
          <template slot-scope="scope">
            <el-popover trigger="hover" placement="top">
              <span>{{ scope.row.title }}</span>
              <div slot="reference" class="name-wrapper buttonText">{{ scope.row.title }}</div>
            </el-popover>
          </template>
        </el-table-column>
        <el-table-column label="" prop="taskformStr" align="center"></el-table-column>
        <el-table-column label="" prop="price" align="center"></el-table-column>
        <el-table-column label="" prop="totalNum" align="center"></el-table-column>
        <el-table-column label="" prop="taskQuantity" align="center"></el-table-column>
        <el-table-column label="" prop="taskStatus" align="center">
          <template slot-scope="scope">
            <span v-if="scope.row.taskStatus==1"></span>
            <span v-if="scope.row.taskStatus==2"></span>
            <span v-if="scope.row.taskStatus==3"></span>
            <span v-if="scope.row.taskStatus==4"></span>
          </template>
        </el-table-column>
        <el-table-column label="" prop="phone" align="center"></el-table-column>
        <el-table-column label="" prop="deptName" align="center"></el-table-column>
        <el-table-column label="()" prop="reserveDay" align="center">
          <template slot-scope="scope">
            <p>{{scope.row.reserveDay | reserveDayfilters }}</p>
          </template>
        </el-table-column>
        <el-table-column label="" prop="publishTime" align="center" sortable></el-table-column>
        <el-table-column label="" prop="cancelTime" align="center" v-if="rwStatus==2" sortable></el-table-column>
        <el-table-column label="" prop="cancelTime" align="center" v-if="rwStatus==3" sortable></el-table-column>
        <el-table-column label="" prop="endTime" align="center" sortable></el-table-column>
      </el-table>
      <!--  -->
      <div>
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="currentPage"
          :page-sizes="pageSizes"
          :page-size="PageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="totalCount"
        ></el-pagination>
      </div>
    </el-main>
  </el-container>
</template>

<script>
import DATA from "./taskpagejson.js"; //
import { getTaskCenterAll } from "@/api/api";
export default {
  name: "TaskPage",
  inject: ["reload"],
  //
  filters: {
    reserveDayfilters(val) {
      if (val == null || val == "" || val == 0) {
        return val;
      } else if (val != 0) {
        return val + "";
      }
    }
  },
  mounted: function() {
    //
    this.tableData = DATA.data.list;
    this.loading = false;

    //this.getTaskCenterAllfunction();
  },
  watch: {},
  data() {
    return {
      //
      loading: true,
      tableData: [],
      // 
      currentPage: 1,
      // ()
      totalCount: 0,
      // 
      pageSizes: [15, 20, 30, 40, 50],
      // 
      PageSize: 15,
      //
      taskId: this.$route.query.taskId,
      rwtitle: "",
      publisher: "",
      rwType: 1,
      rwTypelist: [{ id: 1, name: "" }],
      rwStatus: "",
      rwStatuslist: [
        { id: 1, name: "" },
        { id: 2, name: "" },
        { id: 3, name: "" },
        { id: 4, name: "" }
      ],
      selecttime: "",
      pickerOptions: {
        shortcuts: [
          {
            text: "",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit("pick", [start, end]);
            }
          }
        ]
      }
    };
  },
  methods: {
    //
    getTaskCenterAllfunction: function() {
      this.tableData = [];
      if (this.selecttime == "" || this.selecttime == null) {
        getTaskCenterAll({
          taskId: this.taskId,
          pageNum: this.currentPage,
          pageSize: this.PageSize,
          startTime: "",
          endTime: "",
          taskStatus: this.rwStatus,
          nickName: this.publisher,
          title: this.rwtitle,
          platform: this.rwType
        })
          .then(res => {
            if (res.code == 200) {
              if (res.data == undefined) {
                this.$message.info("!");
              } else {
                this.tableData = res.data.list;
                this.totalCount = res.data.total;
                this.$message.success(res.msg);
              }
            } else {
              this.$message.info("");
            }
            this.loading = false;
          })
          .catch(err => {});
      } else {
        getTaskCenterAll({
          taskId: this.taskId,
          pageNum: this.currentPage,
          pageSize: this.PageSize,
          startTime: this.selecttime[0],
          endTime: this.selecttime[1],
          taskStatus: this.rwStatus,
          nickName: this.publisher,
          title: this.rwtitle,
          platform: this.rwType
        })
          .then(res => {
            if (res.code == 200) {
              if (res.data == undefined) {
                this.$message.info("!");
              } else {
                this.tableData = res.data.list;
                this.totalCount = res.data.total;
                this.$message.success(res.msg);
              }
            } else {
              this.$message.info("");
            }
            this.loading = false;
          })
          .catch(err => {});
      }
    },
    selectTaskCenterAllfunction: function(ins) {
      this.currentPage = 1;
      this.getTaskCenterAllfunction();
    },
    //
    // 
    handleSizeChange(val) {
      // 
      this.PageSize = val;
      // 
      this.currentPage = 1;
      this.getTaskCenterAllfunction();
    },
    // 
    handleCurrentChange(val) {
      // 
      this.currentPage = val;
      this.getTaskCenterAllfunction();
    },
    updataall() {
      this.reload(); //
      this.$route.query.taskId = "";
    }
  }
};
</script>
<style scoped>
.name-wrapper {
  cursor: pointer;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.buttonText {
  text-decoration: none;
  cursor: pointer;
  color: #409eff !important;
}
</style>


