<template>
  <el-container>
    <el-main v-if="homeindex==true">
      <div class="demo-input-suffix" style="margin-bottom:30px;">
        <template>
          :
          <el-input placeholder="" v-model="phone" clearable style="width:160px;"></el-input>
        </template>
        <template>
          :
          <el-select
            v-model="yhType"
            placeholder=""
            @change="selectgetUsersCentergetAllUserfunction()"
          >
            <el-option
              v-for="item in yhTypelist"
              :key="item.id"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </template>
        <template>
          :
          <el-date-picker
            v-model="selecttime"
            type="datetimerange"
            :picker-options="pickerOptions"
            range-separator=""
            start-placeholder=""
            end-placeholder=""
            value-format="yyyy-MM-dd"
            align="center"
          ></el-date-picker>
        </template>

        <el-button
          type="primary"
          icon="el-icon-search"
          @click="selectgetUsersCentergetAllUserfunction()"
        ></el-button>

        <el-button
          type="primary"
          icon="el-icon-refresh"
          circle
          style="float:right;"
          @click="updataall"
        ></el-button>
      </div>
      <el-table
        :data="tableData"
        border
        style="width: 100%"
        v-loading="loading"
        :default-sort="{prop: 'date', order: 'descending'}"
      >
        <el-table-column label="" type="index" width="50" align="center"></el-table-column>
        <el-table-column label="" prop="phone" align="center"></el-table-column>
        <el-table-column label="" prop="alipay" align="center"></el-table-column>
        <el-table-column label="" prop="realName" align="center"></el-table-column>
        <el-table-column label="" prop="totalCount" align="center">
          <template slot-scope="scope">
            <el-popover placement="top" trigger="hover" content="">
              <el-button
                type="text"
                @click="seeMore(scope.row)"
                slot="reference"
              >{{scope.row.totalCount}}</el-button>
            </el-popover>
          </template>
        </el-table-column>
        <el-table-column label="" prop="userStatus" align="center">
          <template slot-scope="scope">
            <span v-if="scope.row.userStatus==1"></span>
            <span v-if="scope.row.userStatus==2"></span>
            <span v-if="scope.row.userStatus==3"></span>
          </template>
        </el-table-column>
        <el-table-column label="" prop="createTime" align="center" sortable></el-table-column>
        <el-table-column label="" align="center" width="200px;">
          <template slot-scope="scope">
            <el-button
              size="small"
              type="danger"
              @click="unbundLing(scope.row.id,scope.row.userStatus)"
              v-if="scope.row.userStatus===1||scope.row.userStatus===2"
            ></el-button>
            <el-button
              size="small"
              type="warning"
              @click="unbundLing(scope.row.id,scope.row.userStatus)"
              v-if="scope.row.userStatus===3"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>

      <!--  -->
      <div>
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="currentPage"
          :page-sizes="pageSizes"
          :page-size="PageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="totalCount"
        ></el-pagination>
      </div>
    </el-main>
    <UserDetailsPage
      :sendid="xquserId"
      :sendids="xquserStatus"
      v-if="showxq==true"
      v-on:listenToChildEvent="showMsgFormChild"
    ></UserDetailsPage>
  </el-container>
</template>


<script>
import DATA from "./userpagejson.js"; //
import UserDetailsPage from "./UserDetailsPage";
import { getUsersCentergetAllUser, postUsersCenterUpdate } from "@/api/api";
export default {
  name: "UserPage",
  inject: ["reload"],
  components: {
    UserDetailsPage
  },

  //getUsersCentergetAllUserfunction,
  mounted: function() {
    //
    this.tableData = DATA.data.list;
    this.loading = false;

    // this.getUsersCentergetAllUserfunction();
  },
  watch: {},
  data() {
    return {
      xquserId: "",
      xquserStatus: "",
      showxq: false,
      homeindex: true,
      yhType: 1,
      yhTypelist: [
        { id: 1, name: "" },
        { id: 2, name: "" },
        { id: 3, name: "" }
      ],
      phone: "",
      loading: true,
      tableData: [],
      // 
      currentPage: 1,
      // ()
      totalCount: 0,
      // 
      pageSizes: [15, 20, 30, 40, 50],
      // 
      PageSize: 15,
      selecttime: "",
      pickerOptions: {
        shortcuts: [
          {
            text: "",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit("pick", [start, end]);
            }
          }
        ]
      }
    };
  },
  methods: {
    showMsgFormChild(data) {
      //v-on
      this.showxq = data; //
      this.homeindex = true;
    },

    //
    getUsersCentergetAllUserfunction: function(insuserStatus) {
      this.tableData = [];
      if (
        (this.selecttime == "" || this.selecttime == null) &&
        (this.phone == "" || this.phone == null)
      ) {
        getUsersCentergetAllUser({
          userStatus: this.yhType,
          pageNum: this.currentPage,
          pageSize: this.PageSize,
          userRole: 1
        })
          .then(res => {
            if (res.code == 200) {
              if (res.data == undefined) {
                this.$message.info("!");
              } else {
                this.tableData = res.data.list;
                this.totalCount = res.data.total;
                this.$message.success(res.data.msg);
              }
            } else {
              this.$message.info("");
            }
          })
          .catch(err => {});
      } else if (
        (this.selecttime == "" || this.selecttime == null) &&
        this.phone != ""
      ) {
        getUsersCentergetAllUser({
          userStatus: this.yhType,
          phone: this.phone,
          pageNum: this.currentPage,
          pageSize: this.PageSize,
          userRole: 1
        })
          .then(res => {
            if (res.code == 200) {
              if (res.data == undefined) {
                this.$message.info("!");
              } else {
                this.tableData = res.data.list;
                this.totalCount = res.data.total;
                this.$message.success(res.data.msg);
              }
            } else {
              this.$message.info("");
            }
          })
          .catch(err => {});
      } else if (this.selecttime != "" && this.phone == "") {
        getUsersCentergetAllUser({
          userStatus: this.yhType,
          pageNum: this.currentPage,
          pageSize: this.PageSize,
          userRole: 1,
          startTime: this.selecttime[0],
          endTime: this.selecttime[1]
        })
          .then(res => {
            if (res.code == 200) {
              if (res.data == undefined) {
                this.$message.info("!");
              } else {
                this.tableData = res.data.list;
                this.totalCount = res.data.total;
                this.$message.success(res.data.msg);
              }
            } else {
              this.$message.info("");
            }
          })
          .catch(err => {});
      } else {
        getUsersCentergetAllUser({
          userStatus: this.yhType,
          phone: this.phone,
          pageNum: this.currentPage,
          pageSize: this.PageSize,
          userRole: 1,
          startTime: this.selecttime[0],
          endTime: this.selecttime[1]
        })
          .then(res => {
            if (res.code == 200) {
              if (res.data == undefined) {
                this.$message.info("!");
              } else {
                this.tableData = res.data.list;
                this.totalCount = res.data.total;
                this.$message.success(res.data.msg);
              }
            } else {
              this.$message.info("");
            }
          })
          .catch(err => {});
      }
      this.loading = false;
    },

    unbundLing(ins, insuserStatus) {
      this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
        center: true
      })
        .then(() => {
          let that = this;
          postUsersCenterUpdate({
            userId: ins,
            userStatus: insuserStatus,
            userRole: 1
          })
            .then(res => {
              if (res.code == 200) {
                that
                  .$confirm(res.msg, "", {
                    confirmButtonText: "",
                    showClose: false,
                    showCancelButton: false,
                    type: "warning",
                    center: true
                  })
                  .then(() => {
                    that.getUsersCentergetAllUserfunction();
                  });
              } else {
                that.$message.info(res.msg);
              }
            })
            .catch(err => {});
        })
        .catch(() => {
          this.$message.info("");
        });
    },

    selectgetUsersCentergetAllUserfunction: function() {
      this.currentPage = 1;
      this.getUsersCentergetAllUserfunction();
    },

    //
    // 
    handleSizeChange(val) {
      // 
      this.PageSize = val;
      // 
      this.currentPage = 1;
      this.getUsersCentergetAllUserfunction();
    },
    // 
    handleCurrentChange(val) {
      // 
      this.currentPage = val;
      this.getUsersCentergetAllUserfunction();
    },

    seeMore: function(row) {
      this.showxq = true;
      this.homeindex = false;
      this.xquserId = row.id;
      this.xquserStatus = row.userStatus;
    },

    updataall() {
      this.reload(); //
    }
  }
};
</script>
<style scoped>
</style>


